export default function PredictiveMaintenanceDashboard() {
  const { t } = useTranslation()
  const [selectedDeviceId, setSelectedDeviceId] = useState<string | null>(null)
  const [isDrawerOpen, setIsDrawerOpen] = useState(false)
  const [timeRange, setTimeRange] = useState('7d')

  const handleDeviceClick = (deviceId: string) => {
    setSelectedDeviceId(deviceId)
    setIsDrawerOpen(true)
  }

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'HIGH':
        return 'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400'
      case 'MEDIUM':
        return 'bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400'
      case 'LOW':
        return 'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-400'
      default:
        return 'bg-slate-100 text-slate-700'
    }
  }

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'ONLINE':
      case 'RESOLVED':
        return <CheckCircle className="w-5 h-5 text-green-500" />
      case 'WARNING':
        return <AlertTriangle className="w-5 h-5 text-yellow-500" />
      case 'CRITICAL':
      case 'ACTIVE':
        return <AlertTriangle className="w-5 h-5 text-red-500" />
      default:
        return <Activity className="w-5 h-5 text-slate-500" />
    }
  }

  return (
    <div className="p-6 space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-slate-900 dark:text-slate-100">
            {t('maintenance.predictiveDashboard')}
          </h1>
          <p className="mt-1 text-sm text-slate-500 dark:text-slate-400">
            {t('maintenance.predictiveDashboardDesc')}
          </p>
        </div>
        <div className="flex items-center gap-2">
          <select
            value={timeRange}
            onChange={(e) => setTimeRange(e.target.value)}
            className="px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg bg-white dark:bg-slate-800 text-slate-700 dark:text-slate-300"
          >
            <option value="24h">{t('common.24h')}</option>
            <option value="7d">{t('common.7d')}</option>
            <option value="30d">{t('common.30d')}</option>
          </select>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <StatCard
          title={t('maintenance.totalDevices')}
          value={mockFleetHealth.totalDevices}
          subtitle={`${mockFleetHealth.online} ${t('maintenance.online')}`}
          icon={Activity}
          trend="+2"
          trendUp={true}
          trendLabel={t('maintenance.vsLastWeek')}
        />
        <StatCard
          title={t('maintenance.averageHealth')}
          value={`${mockFleetHealth.averageHealthScore}%`}
          subtitle={t('maintenance.fleetHealth')}
          icon={Brain}
          trend="-1.2%"
          trendUp={false}
          trendLabel={t('maintenance.vsLastWeek')}
        />
        <StatCard
          title={t('maintenance.predictions')}
          value={mockFleetHealth.predictionsThisWeek}
          subtitle={t('maintenance.thisWeek')}
          icon={TrendingUp}
          trend="+3"
          trendUp={true}
          trendLabel={t('maintenance.vsLastWeek')}
        />
        <StatCard
          title={t('maintenance.scheduled')}
          value={mockFleetHealth.maintenanceScheduled}
          subtitle={t('maintenance.upcoming')}
          icon={Calendar}
          trend="0"
          trendUp={true}
          trendLabel={t('maintenance.vsLastWeek')}
        />
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2 bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700">
          <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100 mb-4">
            {t('maintenance.healthTrend')}
          </h3>
          <div className="h-64">
            <ResponsiveContainer width="100%" height="100%">
              <AreaChart data={mockHealthTrend}>
                <defs>
                  <linearGradient id="colorScore" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.3} />
                    <stop offset="95%" stopColor="#3b82f6" stopOpacity={0} />
                  </linearGradient>
                </defs>
                <CartesianGrid strokeDasharray="3 3" stroke="#e2e8f0" />
                <XAxis dataKey="date" stroke="#64748b" />
                <YAxis stroke="#64748b" domain={[70, 90]} />
                <Tooltip />
                <Legend />
                <Area
                  type="monotone"
                  dataKey="score"
                  name={t('maintenance.avgHealthScore')}
                  stroke="#3b82f6"
                  fillOpacity={1}
                  fill="url(#colorScore)"
                />
                <Bar
                  dataKey="failures"
                  name={t('maintenance.failures')}
                  fill="#ef4444"
                  barSize={20}
                />
              </AreaChart>
            </ResponsiveContainer>
          </div>
        </div>

        <div className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700">
          <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100 mb-4">
            {t('maintenance.riskDistribution')}
          </h3>
          <div className="h-64">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={mockRiskDistribution}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={80}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {mockRiskDistribution.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip />
                <Legend />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 overflow-hidden">
          <div className="p-6 border-b border-slate-200 dark:border-slate-700">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                {t('maintenance.anomalies')}
              </h3>
              <span className="px-3 py-1 bg-red-100 dark:bg-red-900/30 text-red-700 dark:text-red-400 rounded-full text-sm font-medium">
                {mockAnomalies.filter((a) => a.status === 'ACTIVE').length} {t('maintenance.active')}
              </span>
            </div>
          </div>
          <div className="divide-y divide-slate-200 dark:divide-slate-700">
            {mockAnomalies.slice(0, 4).map((anomaly) => (
              <motion.div
                key={anomaly.id}
                whileHover={{ backgroundColor: 'rgba(59, 130, 246, 0.05)' }}
                className="p-4 cursor-pointer"
                onClick={() => handleDeviceClick(anomaly.deviceId)}
              >
                <div className="flex items-start justify-between">
                  <div className="flex items-start gap-3">
                    {getStatusIcon(anomaly.status)}
                    <div>
                      <div className="flex items-center gap-2">
                        <span className="font-mono text-sm text-slate-600 dark:text-slate-400">
                          {anomaly.deviceId}
                        </span>
                        <span
                          className={`px-2 py-0.5 rounded-full text-xs font-medium ${getSeverityColor(
                            anomaly.severity
                          )}`}
                        >
                          {anomaly.severity}
                        </span>
                      </div>
                      <p className="text-sm font-medium text-slate-900 dark:text-slate-100 mt-1">
                        {anomaly.description}
                      </p>
                      <p className="text-xs text-slate-500 mt-1">{anomaly.detectedAt}</p>
                    </div>
                  </div>
                  <ChevronRight className="w-5 h-5 text-slate-400" />
                </div>
              </motion.div>
            ))}
          </div>
        </div>

        <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 overflow-hidden">
          <div className="p-6 border-b border-slate-200 dark:border-slate-700">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                {t('maintenance.predictions')}
              </h3>
              <button
                type="button"
                className="text-sm text-blue-600 hover:text-blue-700 font-medium"
              >
                {t('common.viewAll')}
              </button>
            </div>
          </div>
          <div className="divide-y divide-slate-200 dark:divide-slate-700">
            {mockPredictions.map((prediction) => (
              <motion.div
                key={prediction.id}
                whileHover={{ backgroundColor: 'rgba(59, 130, 246, 0.05)' }}
                className="p-4 cursor-pointer"
                onClick={() => handleDeviceClick(prediction.deviceId)}
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className="relative w-12 h-12">
                      <svg className="w-full h-full transform -rotate-90" role="img" aria-label="Failure Probability">
                        <title>Failure Probability</title>
                        <circle
                          cx="24"
                          cy="24"
                          r="20"
                          stroke="#e2e8f0"
                          strokeWidth="4"
                          fill="none"
                        />
                        <circle
                          cx="24"
                          cy="24"
                          r="20"
                          stroke={prediction.failureProbability > 50 ? '#ef4444' : '#f59e0b'}
                          strokeWidth="4"
                          fill="none"
                          strokeDasharray={`${prediction.failureProbability * 1.26} 126`}
                          strokeLinecap="round"
                        />
                      </svg>
                      <div className="absolute inset-0 flex items-center justify-center">
                        <span className="text-xs font-bold text-slate-700">
                          {prediction.failureProbability}%
                        </span>
                      </div>
                    </div>
                    <div>
                      <span className="font-mono text-sm text-slate-600 dark:text-slate-400">
                        {prediction.deviceId}
                      </span>
                      <p className="text-sm font-medium text-slate-900 dark:text-slate-100">
                        {t('maintenance.estimatedFailure')}: {prediction.estimatedFailureDate}
                      </p>
                      <div className="flex gap-1 mt-1">
                        {prediction.riskFactors.map((factor, idx) => (
                          <span
                            key={idx}
                            className="px-2 py-0.5 bg-slate-100 dark:bg-slate-700 text-slate-600 dark:text-slate-400 rounded text-xs"
                          >
                            {factor}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>
                  <ChevronRight className="w-5 h-5 text-slate-400" />
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>

      <div className="bg-white dark:bg-slate-800 rounded-2xl border border-slate-200 dark:border-slate-700 overflow-hidden">
        <div className="p-6 border-b border-slate-200 dark:border-slate-700">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
              {t('maintenance.schedule')}
            </h3>
            <div className="flex items-center gap-2">
              <button
                type="button"
                className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm font-medium transition-colors"
              >
                <Wrench className="w-4 h-4" />
                {t('maintenance.createTicket')}
              </button>
            </div>
          </div>
        </div>
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-50 dark:bg-slate-700/50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.device')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.typeLabel')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.priority')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.scheduledDate')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.description')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">
                  {t('maintenance.statusLabel')}
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-200 dark:divide-slate-700">
              {mockMaintenanceSchedule.map((schedule) => (
                <tr
                  key={schedule.id}
                  className="hover:bg-slate-50 dark:hover:bg-slate-700/30 cursor-pointer"
                  onClick={() => handleDeviceClick(schedule.deviceId)}
                >
                  <td className="px-6 py-4">
                    <div>
                      <p className="font-mono text-sm text-slate-900 dark:text-slate-100">
                        {schedule.deviceId}
                      </p>
                      <p className="text-xs text-slate-500">{schedule.deviceName}</p>
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        schedule.type === 'PREVENTIVE'
                          ? 'bg-blue-100 text-blue-700'
                          : schedule.type === 'PREDICTIVE'
                          ? 'bg-purple-100 text-purple-700'
                          : 'bg-orange-100 text-orange-700'
                      }`}
                    >
                      {schedule.type}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${getSeverityColor(
                        schedule.priority
                      )}`}
                    >
                      {schedule.priority}
                    </span>
                  </td>
                  <td className="px-6 py-4 text-sm text-slate-600 dark:text-slate-300">
                    {schedule.scheduledDate}
                  </td>
                  <td className="px-6 py-4 text-sm text-slate-600 dark:text-slate-300">
                    {schedule.description}
                  </td>
                  <td className="px-6 py-4">
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        schedule.status === 'SCHEDULED'
                          ? 'bg-blue-100 text-blue-700'
                          : schedule.status === 'IN_PROGRESS'
                          ? 'bg-yellow-100 text-yellow-700'
                          : 'bg-green-100 text-green-700'
                      }`}
                    >
                      {schedule.status}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <button type="button" className="p-2 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-lg">
                      <MoreHorizontal className="w-4 h-4 text-slate-400" />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      <DeviceDetailDrawer
        deviceId={selectedDeviceId}
        isOpen={isDrawerOpen}
        onClose={() => setIsDrawerOpen(false)}
      />
    </div>
  )
}

function StatCard({
  title,
  value,
  subtitle,
  icon: Icon,
  trend,
  trendUp,
  trendLabel,
}: {
  title: string
  value: string | number
  subtitle: string
  icon: any
  trend: string
  trendUp: boolean
  trendLabel: string
}) {
  return (
    <div className="bg-white dark:bg-slate-800 rounded-2xl p-6 border border-slate-200 dark:border-slate-700">
      <div className="flex items-start justify-between">
        <div>
          <p className="text-sm text-slate-500 dark:text-slate-400">{title}</p>
          <p className="text-3xl font-bold text-slate-900 dark:text-slate-100 mt-2">{value}</p>
          <p className="text-sm text-slate-500 mt-1">{subtitle}</p>
        </div>
        <div className="p-3 bg-blue-50 dark:bg-blue-900/20 rounded-xl">
          <Icon className="w-6 h-6 text-blue-600 dark:text-blue-400" />
        </div>
      </div>
      <div className="flex items-center gap-2 mt-4">
        <span className={`flex items-center gap-1 text-sm font-medium ${trendUp ? 'text-green-600' : 'text-red-600'}`}>
          {trendUp ? <TrendingUp className="w-4 h-4" /> : <TrendingDown className="w-4 h-4" />}
          {trend}
        </span>
        <span className="text-sm text-slate-400">{trendLabel}</span>
      </div>
    </div>
  )
}
