import { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { motion, AnimatePresence } from 'framer-motion'
import {
  X,
  Info,
  Activity,
  FileText,
  Settings,
  Brain,
  Battery,
  Thermometer,
  HardDrive,
  Wifi,
  Clock,
  MapPin,
  Cpu,
  Shield,
  AlertTriangle,
  CheckCircle,
  ChevronRight,
  Wrench,
  TrendingUp,
  TrendingDown,
  MoreHorizontal,
  RefreshCw,
  Download,
} from 'lucide-react'
import { LineChart, Line, AreaChart, Area, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts'

interface DeviceDetailDrawerProps {
  deviceId: string | null
  isOpen: boolean
  onClose: () => void
}

type TabType = 'basic' | 'health' | 'logs' | 'operations' | 'predictive'

const mockDeviceData = {
  id: 'DEV-2024-001',
  name: 'Terminal A-101',
  type: 'ONBOARD',
  status: 'ONLINE',
  serialNumber: 'SN2024001',
  manufacturer: 'Decard',
  model: 'P18Q-Pro',
  firmwareVersion: 'v2.4.1',
  purchaseDate: '2024-01-15',
  warrantyExpiry: '2027-01-15',
  location: 'Line 1 - Station A',
  route: 'Route 101',
  lastSync: '2026-02-15 08:30:00',
  battery: 87,
  storage: 65,
  temperature: 42,
  networkLatency: 23,
}

const mockHealthMetrics = [
  { timestamp: '00:00', cpu: 45, memory: 60, temperature: 38 },
  { timestamp: '04:00', cpu: 42, memory: 58, temperature: 37 },
  { timestamp: '08:00', cpu: 65, memory: 72, temperature: 42 },
  { timestamp: '12:00', cpu: 58, memory: 68, temperature: 40 },
  { timestamp: '16:00', cpu: 72, memory: 75, temperature: 44 },
  { timestamp: '20:00', cpu: 55, memory: 62, temperature: 39 },
  { timestamp: '23:59', cpu: 48, memory: 58, temperature: 38 },
]

const mockLogs = [
  { id: 1, timestamp: '2026-02-15 08:30:00', level: 'INFO', message: 'System heartbeat OK', source: 'HealthMonitor' },
  { id: 2, timestamp: '2026-02-15 08:25:00', level: 'WARN', message: 'High temperature detected: 42°C', source: 'ThermalSensor' },
  { id: 3, timestamp: '2026-02-15 08:20:00', level: 'INFO', message: 'Firmware check completed', source: 'UpdateService' },
  { id: 4, timestamp: '2026-02-15 08:15:00', level: 'ERROR', message: 'Network connection timeout', source: 'NetworkManager' },
  { id: 5, timestamp: '2026-02-15 08:10:00', level: 'INFO', message: 'Daily maintenance completed', source: 'Maintenance' },
]

const mockPredictions = {
  riskLevel: 'MEDIUM',
  failureProbability: 23,
  estimatedFailureDate: '2026-03-15',
  healthScore: 78,
  recommendations: [
    { id: 1, priority: 'HIGH', title: 'Battery capacity dropped to 87%', description: 'Recommend scheduling battery check within 30 days' },
    { id: 2, priority: 'MEDIUM', title: 'Storage usage reached 65%', description: 'Recommend clearing log files to free space' },
    { id: 3, priority: 'LOW', title: 'Temperature sensor reading elevated', description: 'Recommend checking device ventilation' },
  ],
}

const sensorData = [
  { name: 'CPU Temp', value: 42, unit: '°C', status: 'normal', icon: Thermometer },
  { name: 'Battery Level', value: 87, unit: '%', status: 'normal', icon: Battery },
  { name: 'Storage Usage', value: 65, unit: '%', status: 'warning', icon: HardDrive },
  { name: 'Network Latency', value: 23, unit: 'ms', status: 'normal', icon: Wifi },
  { name: 'Uptime', value: 720, unit: 'h', status: 'normal', icon: Clock },
]

export default function DeviceDetailDrawer({ deviceId, isOpen, onClose }: DeviceDetailDrawerProps) {
  const { t } = useTranslation()
  const [activeTab, setActiveTab] = useState<TabType>('basic')

  const tabs: { id: TabType; label: string; icon: any }[] = [
    { id: 'basic', label: t('device.tabs.basic'), icon: Info },
    { id: 'health', label: t('device.tabs.health'), icon: Activity },
    { id: 'logs', label: t('device.tabs.logs'), icon: FileText },
    { id: 'operations', label: t('device.tabs.operations'), icon: Settings },
    { id: 'predictive', label: t('device.tabs.predictive'), icon: Brain },
  ]

  const renderTabContent = () => {
    switch (activeTab) {
      case 'basic':
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <InfoCard label={t('device.id')} value={mockDeviceData.id} />
              <InfoCard label={t('device.name')} value={mockDeviceData.name} />
              <InfoCard label={t('device.type')} value={mockDeviceData.type} />
              <InfoCard label={t('device.status')} value={mockDeviceData.status} badge />
              <InfoCard label={t('device.serialNumber')} value={mockDeviceData.serialNumber} />
              <InfoCard label={t('device.manufacturer')} value={mockDeviceData.manufacturer} />
              <InfoCard label={t('device.model')} value={mockDeviceData.model} />
              <InfoCard label={t('device.firmware')} value={mockDeviceData.firmwareVersion} badge />
              <InfoCard label={t('device.purchaseDate')} value={mockDeviceData.purchaseDate} />
              <InfoCard label={t('device.warrantyExpiry')} value={mockDeviceData.warrantyExpiry} />
              <InfoCard label={t('device.location')} value={mockDeviceData.location} />
              <InfoCard label={t('device.route')} value={mockDeviceData.route} />
              <InfoCard label={t('device.lastSync')} value={mockDeviceData.lastSync} />
            </div>

            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
              {sensorData.map((sensor) => (
                <SensorCard key={sensor.name} {...sensor} />
              ))}
            </div>
          </div>
        )

      case 'health':
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-3 gap-4">
              <HealthMetricCard
                title={t('device.health.cpuUsage')}
                value="45%"
                trend="-2%"
                status="normal"
                icon={Cpu}
              />
              <HealthMetricCard
                title={t('device.health.memoryUsage')}
                value="62%"
                trend="+5%"
                status="warning"
                icon={HardDrive}
              />
              <HealthMetricCard
                title={t('device.health.temperature')}
                value="42°C"
                trend="+3°C"
                status="warning"
                icon={Thermometer}
              />
            </div>

            <div className="bg-white dark:bg-slate-800 rounded-xl p-6 border border-slate-200 dark:border-slate-700">
              <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100 mb-4">
                {t('device.health.24hTrend')}
              </h3>
              <div className="h-64">
                <ResponsiveContainer width="100%" height="100%">
                  <AreaChart data={mockHealthMetrics}>
                    <defs>
                      <linearGradient id="colorCpu" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.3} />
                        <stop offset="95%" stopColor="#3b82f6" stopOpacity={0} />
                      </linearGradient>
                      <linearGradient id="colorMemory" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor="#10b981" stopOpacity={0.3} />
                        <stop offset="95%" stopColor="#10b981" stopOpacity={0} />
                      </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke="#e2e8f0" />
                    <XAxis dataKey="timestamp" stroke="#64748b" />
                    <YAxis stroke="#64748b" />
                    <Tooltip />
                    <Legend />
                    <Area
                      type="monotone"
                      dataKey="cpu"
                      name={t('device.health.cpu')}
                      stroke="#3b82f6"
                      fillOpacity={1}
                      fill="url(#colorCpu)"
                    />
                    <Area
                      type="monotone"
                      dataKey="memory"
                      name={t('device.health.memory')}
                      stroke="#10b981"
                      fillOpacity={1}
                      fill="url(#colorMemory)"
                    />
                    <Line
                      type="monotone"
                      dataKey="temperature"
                      name={t('device.health.temperature')}
                      stroke="#f59e0b"
                      strokeWidth={2}
                    />
                  </AreaChart>
                </ResponsiveContainer>
              </div>
            </div>
          </div>
        )

      case 'logs':
        return (
          <div className="space-y-4">
            <div className="flex gap-2 mb-4">
              <button type="button" className="px-3 py-1.5 bg-blue-100 dark:bg-blue-900/30 text-blue-700 dark:text-blue-300 rounded-lg text-sm font-medium">
                {t('device.logs.all')}
              </button>
              <button type="button" className="px-3 py-1.5 bg-slate-100 dark:bg-slate-700 text-slate-700 dark:text-slate-300 rounded-lg text-sm font-medium">
                {t('device.logs.info')}
              </button>
              <button type="button" className="px-3 py-1.5 bg-yellow-100 dark:bg-yellow-900/30 text-yellow-700 dark:text-yellow-300 rounded-lg text-sm font-medium">
                {t('device.logs.warning')}
              </button>
              <button type="button" className="px-3 py-1.5 bg-red-100 dark:bg-red-900/30 text-red-700 dark:text-red-300 rounded-lg text-sm font-medium">
                {t('device.logs.error')}
              </button>
            </div>

            <div className="space-y-2">
              {mockLogs.map((log) => (
                <div
                  key={log.id}
                  className="p-4 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700"
                >
                  <div className="flex items-start justify-between">
                    <div className="flex items-start gap-3">
                      <span
                        className={`px-2 py-0.5 rounded-full text-xs font-medium ${
                          log.level === 'INFO'
                            ? 'bg-blue-100 text-blue-700'
                            : log.level === 'WARN'
                            ? 'bg-yellow-100 text-yellow-700'
                            : 'bg-red-100 text-red-700'
                        }`}
                      >
                        {log.level}
                      </span>
                      <div>
                        <p className="text-sm font-medium text-slate-900 dark:text-slate-100">
                          {log.message}
                        </p>
                        <p className="text-xs text-slate-500 mt-1">
                          {log.source} • {log.timestamp}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )

      case 'operations':
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <button type="button" className="p-4 bg-blue-50 dark:bg-blue-900/20 rounded-xl border border-blue-200 dark:border-blue-800 hover:bg-blue-100 dark:hover:bg-blue-900/30 transition-colors text-left">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-blue-500 rounded-lg">
                    <RefreshCw className="w-5 h-5 text-white" />
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 dark:text-slate-100">
                      {t('device.operations.reboot')}
                    </h4>
                    <p className="text-sm text-slate-500">{t('device.operations.rebootDesc')}</p>
                  </div>
                </div>
              </button>

              <button type="button" className="p-4 bg-green-50 dark:bg-green-900/20 rounded-xl border border-green-200 dark:border-green-800 hover:bg-green-100 dark:hover:bg-green-900/30 transition-colors text-left">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-green-500 rounded-lg">
                    <Download className="w-5 h-5 text-white" />
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 dark:text-slate-100">
                      {t('device.operations.sync')}
                    </h4>
                    <p className="text-sm text-slate-500">{t('device.operations.syncDesc')}</p>
                  </div>
                </div>
              </button>

              <button type="button" className="p-4 bg-purple-50 dark:bg-purple-900/20 rounded-xl border border-purple-200 dark:border-purple-800 hover:bg-purple-100 dark:hover:bg-purple-900/30 transition-colors text-left">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-purple-500 rounded-lg">
                    <Cpu className="w-5 h-5 text-white" />
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 dark:text-slate-100">
                      {t('device.operations.firmware')}
                    </h4>
                    <p className="text-sm text-slate-500">{t('device.operations.firmwareDesc')}</p>
                  </div>
                </div>
              </button>

              <button type="button" className="p-4 bg-orange-50 dark:bg-orange-900/20 rounded-xl border border-orange-200 dark:border-orange-800 hover:bg-orange-100 dark:hover:bg-orange-900/30 transition-colors text-left">
                <div className="flex items-center gap-3">
                  <div className="p-2 bg-orange-500 rounded-lg">
                    <FileText className="w-5 h-5 text-white" />
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900 dark:text-slate-100">
                      {t('device.operations.logs')}
                    </h4>
                    <p className="text-sm text-slate-500">{t('device.operations.logsDesc')}</p>
                  </div>
                </div>
              </button>
            </div>
          </div>
        )

      case 'predictive':
        return (
          <div className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <div className="p-6 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100">
                    {t('device.predictive.healthScore')}
                  </h3>
                  <div className="flex items-center gap-1 text-yellow-600">
                    <TrendingDown className="w-4 h-4" />
                    <span className="text-sm">-2%</span>
                  </div>
                </div>
                <div className="flex items-center gap-4">
                  <div className="relative w-24 h-24">
                    <svg className="w-full h-full transform -rotate-90" role="img" aria-label="Health Score">
                      <title>Health Score</title>
                      <circle
                        cx="48"
                        cy="48"
                        r="40"
                        stroke="#e2e8f0"
                        strokeWidth="8"
                        fill="none"
                      />
                      <circle
                        cx="48"
                        cy="48"
                        r="40"
                        stroke="#f59e0b"
                        strokeWidth="8"
                        fill="none"
                        strokeDasharray={`${mockPredictions.healthScore * 2.51} 251`}
                        strokeLinecap="round"
                      />
                    </svg>
                    <div className="absolute inset-0 flex items-center justify-center">
                      <span className="text-2xl font-bold text-slate-900 dark:text-slate-100">
                        {mockPredictions.healthScore}
                      </span>
                    </div>
                  </div>
                  <div>
                    <p className="text-sm text-slate-500">{t('device.predictive.scoreDescription')}</p>
                    <p className="text-sm font-medium text-yellow-600 mt-1">
                      {t('device.predictive.maintenanceRecommended')}
                    </p>
                  </div>
                </div>
              </div>

              <div className="p-6 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
                <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100 mb-4">
                  {t('device.predictive.failureRisk')}
                </h3>
                <div className="space-y-4">
                  <div>
                    <div className="flex justify-between text-sm mb-1">
                      <span className="text-slate-600">{t('device.predictive.probability')}</span>
                      <span className="font-medium text-slate-900">{mockPredictions.failureProbability}%</span>
                    </div>
                    <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                      <div
                        className="h-full bg-yellow-500 rounded-full"
                        style={{ width: `${mockPredictions.failureProbability}%` }}
                      />
                    </div>
                  </div>
                  <div className="flex items-center gap-2 text-sm text-slate-600">
                    <Clock className="w-4 h-4" />
                    <span>{t('device.predictive.estimatedFailure')}: {mockPredictions.estimatedFailureDate}</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 p-6">
              <h3 className="text-lg font-semibold text-slate-900 dark:text-slate-100 mb-4">
                {t('device.predictive.recommendations')}
              </h3>
              <div className="space-y-3">
                {mockPredictions.recommendations.map((rec) => (
                  <div
                    key={rec.id}
                    className="flex items-start gap-3 p-4 bg-slate-50 dark:bg-slate-700/50 rounded-lg"
                  >
                    <span
                      className={`px-2 py-0.5 rounded text-xs font-medium ${
                        rec.priority === 'HIGH'
                          ? 'bg-red-100 text-red-700'
                          : rec.priority === 'MEDIUM'
                          ? 'bg-yellow-100 text-yellow-700'
                          : 'bg-blue-100 text-blue-700'
                      }`}
                    >
                      {rec.priority}
                    </span>
                    <div className="flex-1">
                      <h4 className="font-medium text-slate-900 dark:text-slate-100">{rec.title}</h4>
                      <p className="text-sm text-slate-500 mt-1">{rec.description}</p>
                    </div>
                    <ChevronRight className="w-5 h-5 text-slate-400" />
                  </div>
                ))}
              </div>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  if (!deviceId) return null

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onClose}
            className="fixed inset-0 bg-black/50 z-40"
          />
          <motion.div
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
            className="fixed right-0 top-0 h-full w-full max-w-4xl bg-slate-50 dark:bg-slate-900 z-50 shadow-2xl overflow-hidden flex flex-col"
          >
            <div className="flex items-center justify-between p-6 bg-white dark:bg-slate-800 border-b border-slate-200 dark:border-slate-700">
              <div>
                <h2 className="text-xl font-bold text-slate-900 dark:text-slate-100">
                  {mockDeviceData.name}
                </h2>
                <p className="text-sm text-slate-500 mt-1">{deviceId}</p>
              </div>
              <button
                type="button"
                onClick={onClose}
                className="p-2 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-lg transition-colors"
              >
                <X className="w-5 h-5 text-slate-500" />
              </button>
            </div>

            <div className="flex border-b border-slate-200 dark:border-slate-700 bg-white dark:bg-slate-800">
              {tabs.map((tab) => {
                const Icon = tab.icon
                return (
                  <button
                    key={tab.id}
                    type="button"
                    onClick={() => setActiveTab(tab.id)}
                    className={`flex items-center gap-2 px-6 py-4 text-sm font-medium transition-colors border-b-2 ${
                      activeTab === tab.id
                        ? 'border-blue-500 text-blue-600'
                        : 'border-transparent text-slate-500 hover:text-slate-700'
                    }`}
                  >
                    <Icon className="w-4 h-4" />
                    {tab.label}
                  </button>
                )
              })}
            </div>

            <div className="flex-1 overflow-y-auto p-6">
              {renderTabContent()}
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  )
}

function InfoCard({ label, value, badge }: { label: string; value: string; badge?: boolean }) {
  return (
    <div className="p-4 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
      <p className="text-xs text-slate-500 uppercase tracking-wider mb-1">{label}</p>
      {badge ? (
        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
          <CheckCircle className="w-3 h-3 mr-1" />
          {value}
        </span>
      ) : (
        <p className="text-sm font-medium text-slate-900 dark:text-slate-100">{value}</p>
      )}
    </div>
  )
}

function SensorCard({ name, value, unit, status, icon: Icon }: any) {
  return (
    <div className="p-4 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
      <div className="flex items-center gap-3 mb-2">
        <div className={`p-2 rounded-lg ${status === 'warning' ? 'bg-yellow-100' : 'bg-blue-100'}`}>
          <Icon className={`w-4 h-4 ${status === 'warning' ? 'text-yellow-600' : 'text-blue-600'}`} />
        </div>
        <span className="text-xs text-slate-500">{name}</span>
      </div>
      <p className="text-xl font-bold text-slate-900 dark:text-slate-100">
        {value}
        <span className="text-sm font-normal text-slate-500 ml-1">{unit}</span>
      </p>
    </div>
  )
}

function HealthMetricCard({ title, value, trend, status, icon: Icon }: any) {
  const isPositive = trend.startsWith('-')
  return (
    <div className="p-6 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
      <div className="flex items-center justify-between mb-4">
        <div className="p-2 bg-blue-100 dark:bg-blue-900/30 rounded-lg">
          <Icon className="w-5 h-5 text-blue-600 dark:text-blue-400" />
        </div>
        <span
          className={`flex items-center gap-1 text-sm font-medium ${
            isPositive ? 'text-green-600' : 'text-red-600'
          }`}
        >
          {isPositive ? <TrendingDown className="w-4 h-4" /> : <TrendingUp className="w-4 h-4" />}
          {trend}
        </span>
      </div>
      <h3 className="text-sm text-slate-500 mb-1">{title}</h3>
      <p className="text-2xl font-bold text-slate-900 dark:text-slate-100">{value}</p>
      <span
        className={`inline-flex items-center mt-2 px-2 py-0.5 rounded-full text-xs font-medium ${
          status === 'normal'
            ? 'bg-green-100 text-green-700'
            : status === 'warning'
            ? 'bg-yellow-100 text-yellow-700'
            : 'bg-red-100 text-red-700'
        }`}
      >
        {status === 'normal' ? 'Normal' : status === 'warning' ? 'Warning' : 'Critical'}
      </span>
    </div>
  )
}
